    padding: 0;
    margin: 0;
    --PINK: rgb(224, 143, 224);
    --HOVER-PINK: rgb(214, 174, 214);
}

body {
    background-color: rgb(29, 29, 29);
    height: 100vh;
    display: flex;
    gap: 20px;
    align-items: center;
    padding: 0 10vw;
}

h1 {
    color: var(--PINK);
}

.todos > p {
    font-size: 18px;
    text-align: center;
    margin-top: 50px;
}

.lists > p {
    font-size: 18px;
    text-align: center;
    margin-top: 10px;
}

.todos li {
    display: flex;
    align-items: flex-start;
}

#todos div:nth-child(2) {
    margin-left: 15px;
    margin-top: -5px;
    margin-right: auto;
}

.todos li {
    margin-top: 10px;
}

li {
    list-style-type: none;
}

.lists, .todos {
    background-color: rgb(136, 136, 136);
    color: white;
    height: 80vh;
    border: none;
    border-radius: 15px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.lists {
    flex: 1 1 20vw;
}

.todos {
    flex: 2 1 40vw;
}

button {
    background-color: var(--PINK);;
    color: white;
    font-size: 15px;
    padding: 5px;
    border: none;
    border-radius: 5px;
    align-self: flex-end;
    margin-top: auto;
    margin-left: 1px;
}

button:hover {
    background-color: var(--HOVER-PINK);
}

.backdrop {
    height: 100vh;
    width: 100vw;
    border-radius: 18px;
    position: absolute;
    right: 0;
    background-color: rgba(255, 255, 255, 0);
    backdrop-filter: blur(3px);
}

.list-popup {
    position: fixed;
    height: 150px;
    width: 350px;
    z-index: 1;
    left: 38vw;
    background-color: white;
    border-radius: 10px;
}

.todo-popup {
    position: fixed;
    height: 650px;
    width: 350px;
    z-index: 1;
    left: 38vw;
    background-color: white;
    border-radius: 10px;
}

#list-form {
    height: 150px;
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 12px;
    padding: 0 25px;
    font-size: 18px;
    font-weight: 700;
}

#todo-form {
    height: 650px;
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 12px;
    padding: 0 25px;
    font-size: 18px;
    font-weight: 700;
}

.name, .title, .description, .due-date, .priority, .list {
    display: flex;
    flex-direction: column;
}

form input {
    height: 20px;
}

form button {
    font-size: 14px;
    padding: 3px;
}

form div:last-child {
    margin: 0 auto;
}

.lists > ul > div {
    color: white;
    list-style: none;
    font-size: 20px;
    padding: 12px 0;
    border-bottom: white solid;
    display: flex;
}

.lists > ul > div > li {
    margin-right: auto;
}

.lists li:hover {
    color: var(--PINK);
}

ul {
    overflow: scroll;
}

.completed {
    text-decoration: line-through;
    opacity: 0.5;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,UAAU;IACV,SAAS;IACT,0BAA0B;IAC1B,gCAAgC;AACpC;;AAEA;IACI,iCAAiC;IACjC,aAAa;IACb,aAAa;IACb,SAAS;IACT,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,oCAAoC;IACpC,YAAY;IACZ,YAAY;IACZ,YAAY;IACZ,mBAAmB;IACnB,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,6BAA6B;IAC7B,YAAY;IACZ,eAAe;IACf,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,oBAAoB;IACpB,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,mCAAmC;AACvC;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,mBAAmB;IACnB,kBAAkB;IAClB,QAAQ;IACR,wCAAwC;IACxC,0BAA0B;AAC9B;;AAEA;IACI,eAAe;IACf,aAAa;IACb,YAAY;IACZ,UAAU;IACV,UAAU;IACV,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,aAAa;IACb,YAAY;IACZ,UAAU;IACV,UAAU;IACV,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,SAAS;IACT,eAAe;IACf,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,SAAS;IACT,eAAe;IACf,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,eAAe;IACf,eAAe;IACf,0BAA0B;IAC1B,aAAa;AACjB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,6BAA6B;IAC7B,YAAY;AAChB","sourcesContent":["* {\n    padding: 0;\n    margin: 0;\n    --PINK: rgb(224, 143, 224);\n    --HOVER-PINK: rgb(214, 174, 214);\n}\n\nbody {\n    background-color: rgb(29, 29, 29);\n    height: 100vh;\n    display: flex;\n    gap: 20px;\n    align-items: center;\n    padding: 0 10vw;\n}\n\nh1 {\n    color: var(--PINK);\n}\n\n.todos > p {\n    font-size: 18px;\n    text-align: center;\n    margin-top: 50px;\n}\n\n.lists > p {\n    font-size: 18px;\n    